import React, { useState } from 'react';
import './App.css';
import { Switch, Button } from 'antd';

function App() {
  const [open, setOpen] = useState(true);

  // 
  function onChange(e: boolean | ((prevState: boolean) => boolean)) {
    setOpen(e);
    console.log(e)
  }

  //  
  function toAdminPage() {
    // @ts-ignore
    chrome.tabs.create({ url: '../packages/options/build/index.html' })
  }
  return (
    <div className="App">
      <div><Switch checked={open} onChange={onChange} /> Editor Header</div>
      <div> <Button type="primary" block onClick={toAdminPage}></Button></div>
    </div>
  );
}

export default App;
